/* Global styles */
body {
  font-family: 'Segoe UI', sans-serif;
  background: linear-gradient(120deg, #4facfe, #00f2fe);
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

/* Login Container */
.login-container {
  background: white;
  max-width: 400px;
  margin: 100px auto;
  padding: 40px 30px;
  border-radius: 20px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
  text-align: center;
  transition: transform 0.3s ease;
}

.login-container:hover {
  transform: translateY(-5px);
}

.login-container h2 {
  color: #0077b6;
  font-size: 26px;
  margin-bottom: 25px;
}

.login-container input {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 12px;
  font-size: 15px;
  transition: all 0.2s;
}

.login-container input:focus {
  outline: none;
  border-color: #00b4d8;
  box-shadow: 0 0 8px rgba(0, 180, 216, 0.5);
}

.login-container button {
  width: 100%;
  padding: 12px;
  background: linear-gradient(to right, #0077b6, #00b4d8);
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s;
}

.login-container button:hover {
  background: linear-gradient(to right, #00b4d8, #0096c7);
  transform: scale(1.05);
}

/* Dashboard Container */
.dashboard {
  background: #f4f9ff;
  min-height: 100vh;
  padding: 60px 40px;
  animation: fadeIn 0.8s ease-in;
}

/* Dashboard Card */
.dashboard-card {
  background: white;
  max-width: 900px;
  margin: auto;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.dashboard h1 {
  color: #023e8a;
  font-size: 32px;
  margin-bottom: 10px;
}

.dashboard p {
  color: #555;
  font-size: 16px;
  margin-bottom: 25px;
}

/* Table Styling */
table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
  animation: slideUp 0.8s ease;
}

th {
  background-color: #0077b6;
  color: white;
  font-size: 16px;
  padding: 15px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

td {
  padding: 14px;
  border-bottom: 1px solid #ddd;
  color: #333;
  font-size: 15px;
}

tr:hover {
  background-color: #e3f2fd;
  transition: 0.3s;
}

td:nth-child(2) {
  font-weight: bold;
  color: #0077b6;
}

td:nth-child(3) {
  color: #f77f00;
}

/* Add animation for smoother feel */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

/* Responsive Design */
@media (max-width: 768px) {
  .dashboard-card {
    padding: 20px;
  }

  table {
    font-size: 14px;
  }
}
