.hostel-login-page {
  height: 100vh;
  background: linear-gradient(135deg, #8e2de2, #4a00e0);
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-card {
  background: white;
  padding: 40px;
  border-radius: 15px;
  width: 350px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.2);
  text-align: center;
}
.login-card h2 {
  color: #4a00e0;
}
.login-card button {
  background: #8e2de2;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 10px;
  font-weight: bold;
  cursor: pointer;
}
.login-card button:hover {
  background: #4a00e0;
}
.hostel-dashboard {
  background: #f9f6ff;
  min-height: 100vh;
  padding: 40px;
  text-align: center;
}
.hostel-dashboard h1 {
  color: #4a00e0;
}
table {
  width: 80%;
  margin: 30px auto;
  border-collapse: collapse;
  background: white;
  border-radius: 10px;
  overflow: hidden;
}
th {
  background: #8e2de2;
  color: white;
}
th, td {
  padding: 12px;
  border: 1px solid #ddd;
}
tr:hover {
  background-color: #f3e9ff;
}
