/* General page background */
body {
  background-color: #f5f7fa;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
}

/* Dashboard wrapper centers everything */
.dashboard-wrapper {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  padding-top: 50px;
}

/* Dashboard card */
.dashboard-card {
  background-color: #fff;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  width: 80%;
  max-width: 900px;
  padding: 30px 40px;
  transition: 0.3s;
}

.dashboard-card:hover {
  transform: translateY(-4px);
}

/* Title */
.dashboard-title {
  color: #0b4aa1;
  font-size: 26px;
  margin-bottom: 25px;
  font-weight: 700;
  text-align: center;
}

/* Table */
.dashboard-table {
  width: 100%;
  border-collapse: collapse;
  border-radius: 10px;
  overflow: hidden;
}

.dashboard-table th {
  background-color: #0b4aa1;
  color: #fff;
  text-align: left;
  padding: 14px 16px;
  font-size: 15px;
}

.dashboard-table td {
  padding: 14px 16px;
  border-bottom: 1px solid #ddd;
  font-size: 15px;
}

.dashboard-table tr:hover {
  background-color: #f1f4ff;
}

/* Row highlight by status */
.cleared td {
  background-color: #e8f9e9;
  color: #256029;
}

.pending td {
  background-color: #fff5f5;
  color: #b81d13;
}
